.def-modal {
	transition: background 0.8s, z-index 0s 0.8s;
	background: rgba(50,50,50,0);
	z-index: -50;
	position: fixed;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	&.modal-active{
		transition: all 0s;
		z-index: 450;
		background: rgba(50,50,50,0.4);
		.def-modal__outer-container{
			transition: all 0.8s;
			transform: translateY(-100%);
		}
	}
	&__outer-container {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		transition: all 0.8s; // after .8s to allow the model to disappear
		// width: 100%;
		height: 100%;
		top: 100%;
		left: 0;
		overflow-y: scroll;
		// padding: 20px 25px;
		box-sizing: border-box;
		z-index: 5;
		-ms-overflow-style: none;  /* IE and Edge */
		scrollbar-width: none;  /* Firefox */
		&::-webkit-scrollbar {
			display: none;
		}
	}
	&__inner-container {
		margin: auto;
		padding: 50px 20px;
		//max-height: 100%;
		&--100{
			width: 100%;
		}
		&--75{
			width: 75%;
			@media screen and (max-width: 772px) {
				width: 100%;
			}
		}
		&--50{
			width: 50%;
			@media screen and (max-width: 992px) {
				width: 75%;
			}
			@media screen and (max-width: 772px) {
				width: 100%;
			}
		}
	}
	&__wrapper {
		position: relative;
		z-index: 10;
		background: #FFF;
		border-radius: 4px;
		padding: 15px 25px;
	}
	&__top {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	&__title {
		justify-self: start;
	}

	&__cross {
		justify-self: end;
		cursor: pointer;
		height: 20px;
		// width: 20px;
		flex: 0 0 20px;
		position: relative;
		&:before{
			content: '';
			position: absolute;
			width: 100%;
			height: 100%;
			border-radius: 50%;
			background: $darkGreen;
			z-index: -1;
		}
	}
	&__cross-line {
		position: absolute;
		width: 0;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		margin: auto;
		height: 50%;
		box-sizing: border-box;
		border: 1px solid $white;
		background: $white;
		border-radius: 2px;
		transform-origin: 50% 50%;
		&--1 {
			transform: rotate(-135deg);
		}
		&--2 {
			transform: rotate(135deg);
		}
	}
	&__content-wrapper{
		.large-text{
			overflow: auto;
			max-height: min(70vh, 400px);
		}
	}
}