
//RobotoCondensed

@font-face {
	font-family: RobotoCondensed;
	font-weight: 300;
	src: url(../fonts/RobotoCondensed/RobotoCondensed-Light.ttf);
}
@font-face {
	font-family: RobotoCondensed;
	font-weight: 400;
	src: url(../fonts/RobotoCondensed/RobotoCondensed-Regular.ttf);
}
@font-face {
	font-family: RobotoCondensed;
	font-weight: 700;
	src: url(../fonts/RobotoCondensed/RobotoCondensed-Bold.ttf);
}



